import http from '../../common/http.js';
let money = 0;
let level = 0;
export default {
	data() {
		return {
			borderStyle: "",
			borderSecondStyle: "",
			borderThirdStyle: "",
			scrollHeight: 0,
			comboHeight: 0,
			rightViewTop: 0,
			rightViewBottom: 0,
			secondLineMarginTop: 0,
			yuedu_money: 0,
			jidu_money: 0,
			niandu_money: 0,
		}
	},
	computed: {
		userInfo: function() {
			return this.$store.getters.userInfo;
		}
	},
	onShareAppMessage() {
		let pages = getCurrentPages() //获取加载的页面
		let currentPage = pages[pages.length-1] //获取当前页面的对象
		let url = currentPage.route //当前页面url
		return {
			title: '升级VIP',
			path: url,
			success: function() {
			},
			fail: function() {
			}
		};
	},
	onLoad() {
		uni.showShareMenu({
		    withShareTicket: true
		});
		let scrollHeight  = uni.getSystemInfoSync().windowHeight;
		let winWidth = uni.getSystemInfoSync().windowWidth;
		//console.log(scrollHeight);
		let ratio = 750/winWidth;
		ratio = ratio.toFixed(2);
		//this.scrollHeight = 145*3*ratio+20; 
		this.comboHeight  = 120*ratio;
		this.rightViewTop = 25*ratio;
		this.rightViewBottom = 28*ratio;
		this.secondLineMarginTop = 9*ratio;
		//console.log('comboHeight', this.comboHeight);
	},
	mounted() {
		this.initVipMoney();
	},
	methods: {
		initVipMoney() {
			let userInfo = this.userInfo;
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			const url = apiPrefix + "?mod=user&ac=get_vip_jine";
			http.request(url, {
				auth: auth,
			}).then(resp=>{
				this.yuedu_money = resp.yue;
				this.jidu_money  = resp.ji;
				this.niandu_money = resp.nian;
				//this.selectCombo();
			});
		},
		selectCombo() {
			this.borderStyle = "2px solid rgba(249,177,127,1);";
			this.borderSecondStyle = "";
			this.borderThirdStyle  = "";
			//money = 0.01;
			money = this.yuedu_money;
			level = 2;
			this.comboPay();
		},
		selectCombo2() {
			this.borderStyle = "";
			this.borderSecondStyle = "2px solid rgba(249,177,127,1);";
			this.borderThirdStyle  = "";
			money = this.jidu_money;
			//money = 0.01;
			level = 3;
			this.comboPay();
		},
		selectCombo3() {
			this.borderStyle = "";
			this.borderSecondStyle = "";
			this.borderThirdStyle  = "2px solid rgba(249,177,127,1);";
			money = this.niandu_money;
			//money = 0.01;
			level = 4;
			this.comboPay();
		},
		wxPay() {
			this.comboPay();
		},
		generateSign(obj) {
			const data = getApp().globalData;
			const key = data.key;
			let str = "appId="+obj.appId+"&nonceStr="+obj.nonceStr+"&package="+obj.package+
			"&signType="+obj.signType+'&timeStamp='+obj.timeStamp+'&key='+key;
			return md5(str).toUpperCase();
		},
		comboPay() {
			let userInfo = this.userInfo;
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			const url = apiPrefix + "?mod=user&ac=unifiedorder";
			http.request(url, {
				auth: auth,
				money: money,
				uid: userInfo.uid,
				level: level,
				filterData: true
			}).then(resp => {
				//console.log('resp', resp);
				uni.requestPayment({
					appId: resp.appid,
					timeStamp: resp.timeStamp,
					nonceStr: resp.nonce_str,
					package: resp.prepay_id,
					signType: 'MD5',
					paySign: resp.paySign2,
					success: function(res) {
						//console.log('success:' + JSON.stringify(res));
					},
					fail: function(err) {
						//console.log('fail:' + JSON.stringify(err));
						uni.showModal({
							title: '提示',
							content: '充值失败!',
							showCancel: false,
							cancelText: '',
							confirmText: '确定',
							success: res2 => {
								
							}
						});
					},
					complete: (res) => {
						let msg = '';
						if(res.errMsg == 'requestPayment:ok') {
							msg = '充值成功!';
							uni.showModal({
								title: '提示',
								content: msg,
								showCancel: false,
								cancelText: '',
								confirmText: '确定',
								success: res2 => {
									if(res2.confirm) {
										getApp().globalData.isRecharge = 1;
										uni.switchTab({
											url: '/pages/user/index'
										});
									}
								}
							});
						}
					}
				});
			});
			
			
		}
	}
}
